import Card from "react-bootstrap/Card";
import Nav from "react-bootstrap/Nav";
import Button from "react-bootstrap/Button";
import AuthContext from "../../../store/context/auth-context";
import {useNavigate} from "react-router-dom";


import React, {useContext, useState} from "react";
import DropdownButton from "react-bootstrap/DropdownButton";
import Dropdown from "react-bootstrap/Dropdown";

export default function BoardCard(props: any) {
    const authCtx = useContext(AuthContext);
    const [direction, setDirection] = useState("desc");
    const navigate = useNavigate();

    let categoryHandler = (eventkey: any) => {
        if (eventkey === props.initialParams.boardCategory) {
            return
        }
        props.initialCategoryHandler(eventkey);
        props.initialPageHandler(0);
        props.initialSearchHandler(null, null, null);
        props.setParams(props.initialParams);
    }

    let directionHandler = (eventkey: any) => {
        console.log(props.initialParams.direction)
        if (eventkey === props.initialParams.direction) {
            return
        }
        props.initialDirectionHandler(eventkey);
        props.initialPageHandler(0);
        props.setParams(props.initialParams);
        setDirection(eventkey);
    }

    const toNewBoard = () => {
        navigate("/board/new");
    }

    return (
        <Card className={"mb-3"}>
            <Card.Header>
                <div className="d-flex w-200 justify-content-between">
                <Nav variant="tabs" defaultActiveKey="total" onSelect={categoryHandler}>
                    <Nav.Item>
                        <Nav.Link eventKey="total"></Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="free"></Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="notice"></Nav.Link>
                    </Nav.Item>
                </Nav>
                    <DropdownButton
                        variant="outline-secondary"
                        title={direction === "desc" ? "" : ""}
                        id="input-group-dropdown-1"
                        onSelect={directionHandler}
                    >
                        <Dropdown.Item eventKey="desc"></Dropdown.Item>
                        <Dropdown.Item eventKey="asc"></Dropdown.Item>
                    </DropdownButton>
                </div>
            </Card.Header>
            <Card.Body>
                <Card.Title>
                    <h2></h2>
                </Card.Title>
                <div className="d-flex w-200 justify-content-between">
                    {authCtx.isLoggedIn ? <Button variant="primary" onClick={toNewBoard}> </Button> :  <Button variant="secondary" disabled={false} onClick={toNewBoard}>  </Button>}
                   <Button variant="primary" onClick={props.refreshHandler}></Button>
                </div>
            </Card.Body>
        </Card>
    )
}
